<template>
  <div class="player">
    <p>
      {{ a }}
    </p>
    <audio ref="audioRef" controls autoPlay :src="audioURL" loop></audio>
  </div>
</template>
<script>
import bus from '@/bus/bus'
export default {
  //https://autumnfish.cn/song/detail?ids
  mounted () {
    bus.$on('toPlay', async id => {
      const res = await this.$axios.get(`song/url?id=${id}`)
      this.audioURL = res.data[0].url
      const res2 = await this.$axios.get(
        `https://autumnfish.cn/song/detail?ids=${id}`
      )
      this.a = res2.songs[0].name
    })
    bus.$on('pauseMusic', () => {
      this.$refs.audioRef.pause()
    })
  },
  data () {
    return {
      audioURL: '',
      a: '暂无歌曲'
    }
  }
}
</script>
<style lang="less" scoped>
.player {
  text-align: center;
  box-shadow: 0 20px 20px 20px rgb(99, 98, 98);
  background: #f1f3f4;
  height: 60px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  p {
    text-align: center;
    float: left;
    height: 60px;
    line-height: 55px;
    width: 10%;
  }
}
audio {
  height: 56px;
  float: right;
  width: 90%;
  border-radius: none;
  outline: none;
}
</style>
