import Vue from 'vue'
import VueRouter from 'vue-router'

import findMusic from '@/views/findMusic'
import recList from '@/views/recList'
import newMusic from '@/views/newMusic'
import newMv from '@/views/newMv'
import notFound from '@/views/notFound'
import musciList from '@/views/musciList'
import recMv from '@/views/recMv'

Vue.use(VueRouter)

let router = new VueRouter({
  routes: [
    {
      path: '/findMusic',
      component: findMusic
    },
    {
      path: '/recList',
      component: recList
    },
    {
      path: '/newMusic',
      component: newMusic
    },
    {
      path: '/newMv',
      component: newMv
    },
    {
      path: '/musciList',
      component: musciList
    },
    {
      path: '/recMv/:id',
      component: recMv
    },
    {
      path: '/',
      redirect: '/findMusic'
    },
    {
      path: '*',
      component: notFound
    }
  ]
})

export default router
