import Vue from 'vue'
import App from './App.vue'

import axios from 'axios'
import '@/assets/style/global.less'
import ElementUI, { Loading } from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from '@/router'

Vue.config.productionTip = false
Vue.use(ElementUI)

// http://www.dongh5.com:9000/、http://huangjiangjun.top:9000/
axios.defaults.baseURL = 'http://www.dongh5.com:9000/'

let loadingInstance = null
// 请求拦截器
axios.interceptors.request.use(
  function (config) {
    loadingInstance = Loading.service({
      text: '拼命加载中...',
      spinner: 'el-icon-loading'
    })
    return config
  },
  function (error) {
    // 对请求错误做些什么l
    return Promise.reject(error)
  }
)

//响应拦截
axios.interceptors.response.use(
  function (response) {
    // 对响应数据做点什么
    loadingInstance.close()
    return response.data
  },
  function (error) {
    // 对响应错误做点什么
    return Promise.reject(error)
  }
)

Vue.filter('formatCount', val => {
  return val / 10000 > 1 ? Math.ceil(val / 10000) + '万' : val
})
Vue.filter('formatDuration', dt => {
  // 转分
  let min = Math.ceil(dt / 1000 / 60)
  min = min < 10 ? '0' + min : min
  // 秒
  let sec = Math.ceil((dt / 1000) % 60)
  sec = sec < 10 ? '0' + sec : sec
  return min + ':' + sec
})

Vue.prototype.$axios = axios

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
